.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
  }
  
  &__star:last-child {
    margin: 0;
  }
  
  @for $index from 1 through 5 {
    &--#{$index} &__star:nth-child(-n+#{$index}) {
      background-image: url("../images/star-active.svg");
    }
  }
  
}
